import { __, _e } from '@wordpress/i18n';
import faqLogo from '../assets/images/icon-256x256.png';

const Header = () => {
    return (
        <header className="pfaqm-header">
            <div className="pfaqm-header-wrapper">
                <div><img src={faqLogo} alt="Product FAQs Manager" /></div>
                <div>
                    <h1>{__('Product FAQs Manager', 'product-faq-manager')}</h1>
                    <p>Enhance your WooCommerce store with the Product FAQs Manager!</p>
                </div>
            </div>
        </header>
    );
};

export default Header;